'use client';

import { useEffect, useState } from 'react';
import { useAuth } from '@/context/AuthContext';
import { useRouter } from 'next/navigation';
import { Card } from '@/components/ui/Card';
import { Button } from '@/components/ui/Button';
import { Input } from '@/components/ui/Input';

interface Doctor {
  id: string;
  name: string;
  email: string;
  role: string;
  patientsCount?: number;
}

interface DoctorWithPatients extends Doctor {
  patients: Array<{
    _id?: string;
    id?: string;
    name: string;
    email?: string;
    contact?: string;
  }>;
}

export default function SuperAdminPage() {
  const { user } = useAuth();
  const router = useRouter();
  const [doctors, setDoctors] = useState<Doctor[]>([]);
  const [selectedDoctor, setSelectedDoctor] = useState<DoctorWithPatients | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [showCreateModal, setShowCreateModal] = useState(false);
  const [createForm, setCreateForm] = useState({
    name: '',
    email: '',
    password: '',
    role: 'doctor'
  });
  const [message, setMessage] = useState('');

  useEffect(() => {
    // Redirect non-admin users to regular dashboard
    if (user && user.role !== 'admin') {
      router.push('/dashboard');
      return;
    }
    
    if (user && user.role === 'admin') {
      loadDoctors();
    }
  }, [user, router]);

  const loadDoctors = async () => {
    try {
      setIsLoading(true);
      const response = await fetch(`${process.env.NEXT_PUBLIC_API_URL}/users?role=doctor`, {
        headers: { 'Authorization': `Bearer ${localStorage.getItem('token')}` }
      });
      const data = await response.json();
      
      // Get patient count for each doctor
      const doctorsWithCounts = await Promise.all(
        data.map(async (doctor: Doctor) => {
          const patientsRes = await fetch(`${process.env.NEXT_PUBLIC_API_URL}/patients?doctorId=${doctor.id}`, {
            headers: { 'Authorization': `Bearer ${localStorage.getItem('token')}` }
          });
          const patients = await patientsRes.json();
          return { ...doctor, patientsCount: patients.length };
        })
      );
      
      setDoctors(doctorsWithCounts);
    } catch (error) {
      console.error('Failed to load doctors:', error);
    } finally {
      setIsLoading(false);
    }
  };

  const loadDoctorPatients = async (doctorId: string) => {
    try {
      const [doctorRes, patientsRes] = await Promise.all([
        fetch(`${process.env.NEXT_PUBLIC_API_URL}/users?role=doctor`, {
          headers: { 'Authorization': `Bearer ${localStorage.getItem('token')}` }
        }),
        fetch(`${process.env.NEXT_PUBLIC_API_URL}/patients?doctorId=${doctorId}`, {
          headers: { 'Authorization': `Bearer ${localStorage.getItem('token')}` }
        })
      ]);

      const allDoctors = await doctorRes.json();
      const patients = await patientsRes.json();
      const doctor = allDoctors.find((d: Doctor) => d.id === doctorId);

      console.log('Doctor:', doctor);
      console.log('Patients:', patients);

      if (doctor) {
        setSelectedDoctor({
          ...doctor,
          patientsCount: patients.length,
          patients: patients || []
        });
      }
    } catch (error) {
      console.error('Failed to load doctor patients:', error);
      setMessage('Failed to load doctor patients');
      setTimeout(() => setMessage(''), 3000);
    }
  };

  const handleCreateDoctor = async (e: React.FormEvent) => {
    e.preventDefault();
    try {
      const response = await fetch(`${process.env.NEXT_PUBLIC_API_URL}/users`, {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${localStorage.getItem('token')}`,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(createForm)
      });

      if (response.ok) {
        setMessage('Doctor created successfully!');
        setShowCreateModal(false);
        setCreateForm({ name: '', email: '', password: '', role: 'doctor' });
        loadDoctors();
      } else {
        const error = await response.json();
        setMessage(error.error || 'Failed to create doctor');
      }
    } catch (error) {
      console.error('Create doctor error:', error);
      setMessage('Failed to create doctor');
    }
    setTimeout(() => setMessage(''), 3000);
  };

  if (isLoading) {
    return <div className="flex items-center justify-center min-h-screen">Loading...</div>;
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Super Admin Dashboard</h1>
          <p className="text-gray-600 mt-1">Manage doctors and monitor their patients</p>
        </div>
        <Button
          onClick={() => setShowCreateModal(true)}
          className="bg-gradient-to-r from-blue-600 to-indigo-600 hover:from-blue-700 hover:to-indigo-700 text-white px-6 py-3 rounded-lg font-semibold shadow-lg hover:shadow-xl transition-all flex items-center gap-2"
        >
          <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 6v6m0 0v6m0-6h6m-6 0H6" />
          </svg>
          Create Doctor
        </Button>
      </div>

      {/* Message */}
      {message && (
        <div className={`p-4 rounded-lg ${message.includes('success') ? 'bg-green-100 text-green-800' : 'bg-red-100 text-red-800'}`}>
          {message}
        </div>
      )}

      {/* Stats Cards */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        <Card className="bg-gradient-to-br from-blue-500 to-blue-600 border-0 shadow-xl">
          <div className="p-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-blue-100 text-sm font-medium">Total Doctors</p>
                <p className="text-4xl font-bold text-white mt-2">{doctors.length}</p>
              </div>
              <div className="bg-white bg-opacity-20 p-4 rounded-xl">
                <svg className="w-8 h-8 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M16 7a4 4 0 11-8 0 4 4 0 018 0zM12 14a7 7 0 00-7 7h14a7 7 0 00-7-7z" />
                </svg>
              </div>
            </div>
          </div>
        </Card>

        <Card className="bg-gradient-to-br from-green-500 to-green-600 border-0 shadow-xl">
          <div className="p-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-green-100 text-sm font-medium">Total Patients</p>
                <p className="text-4xl font-bold text-white mt-2">
                  {doctors.reduce((sum, d) => sum + (d.patientsCount || 0), 0)}
                </p>
              </div>
              <div className="bg-white bg-opacity-20 p-4 rounded-xl">
                <svg className="w-8 h-8 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M17 20h5v-2a3 3 0 00-5.356-1.857M17 20H7m10 0v-2c0-.656-.126-1.283-.356-1.857M7 20H2v-2a3 3 0 015.356-1.857M7 20v-2c0-.656.126-1.283.356-1.857m0 0a5.002 5.002 0 019.288 0M15 7a3 3 0 11-6 0 3 3 0 016 0zm6 3a2 2 0 11-4 0 2 2 0 014 0zM7 10a2 2 0 11-4 0 2 2 0 014 0z" />
                </svg>
              </div>
            </div>
          </div>
        </Card>

        <Card className="bg-gradient-to-br from-purple-500 to-purple-600 border-0 shadow-xl">
          <div className="p-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-purple-100 text-sm font-medium">Avg Patients/Doctor</p>
                <p className="text-4xl font-bold text-white mt-2">
                  {doctors.length > 0 
                    ? Math.round(doctors.reduce((sum, d) => sum + (d.patientsCount || 0), 0) / doctors.length)
                    : 0
                  }
                </p>
              </div>
              <div className="bg-white bg-opacity-20 p-4 rounded-xl">
                <svg className="w-8 h-8 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 19v-6a2 2 0 00-2-2H5a2 2 0 00-2 2v6a2 2 0 002 2h2a2 2 0 002-2zm0 0V9a2 2 0 012-2h2a2 2 0 012 2v10m-6 0a2 2 0 002 2h2a2 2 0 002-2m0 0V5a2 2 0 012-2h2a2 2 0 012 2v14a2 2 0 01-2 2h-2a2 2 0 01-2-2z" />
                </svg>
              </div>
            </div>
          </div>
        </Card>
      </div>

      {/* Doctors List */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Doctors Cards */}
        <div className="space-y-4">
          <div className="flex items-center justify-between">
            <h2 className="text-2xl font-bold text-gray-900">Doctors</h2>
            <p className="text-sm text-gray-500 italic">Click on a doctor to view patients</p>
          </div>
          {doctors.length === 0 ? (
            <Card>
              <div className="p-8 text-center text-gray-500">
                No doctors found. Create your first doctor to get started.
              </div>
            </Card>
          ) : (
            doctors.map((doctor) => (
              <Card 
                key={doctor.id} 
                className={`transition-all cursor-pointer ${
                  selectedDoctor?.id === doctor.id 
                    ? 'ring-2 ring-blue-500 shadow-xl bg-blue-50' 
                    : 'hover:shadow-lg hover:bg-gray-50'
                }`}
                onClick={() => loadDoctorPatients(doctor.id)}
              >
                <div className="p-6">
                  <div className="flex items-center justify-between">
                    <div className="flex items-center gap-4">
                      <div className={`w-12 h-12 rounded-full flex items-center justify-center ${
                        selectedDoctor?.id === doctor.id
                          ? 'bg-gradient-to-br from-blue-600 to-indigo-600 ring-2 ring-blue-300'
                          : 'bg-gradient-to-br from-blue-500 to-indigo-500'
                      }`}>
                        <span className="text-white text-lg font-bold">
                          {doctor.name.charAt(0).toUpperCase()}
                        </span>
                      </div>
                      <div>
                        <h3 className={`text-lg font-bold ${
                          selectedDoctor?.id === doctor.id ? 'text-blue-700' : 'text-gray-900'
                        }`}>{doctor.name}</h3>
                        <p className="text-sm text-gray-600">{doctor.email}</p>
                      </div>
                    </div>
                    <div className="text-center">
                      <p className={`text-3xl font-bold ${
                        selectedDoctor?.id === doctor.id ? 'text-blue-700' : 'text-blue-600'
                      }`}>{doctor.patientsCount || 0}</p>
                      <p className="text-xs text-gray-500">Patients</p>
                    </div>
                  </div>
                </div>
              </Card>
            ))
          )}
        </div>

        {/* Selected Doctor's Patients */}
        <div className="space-y-4">
          <h2 className="text-2xl font-bold text-gray-900">
            {selectedDoctor ? (
              <span className="flex items-center gap-2">
                <span className="text-blue-600">{selectedDoctor.name}'s</span> Patients
                <span className="text-sm font-normal text-gray-500">({selectedDoctor.patients.length})</span>
              </span>
            ) : (
              'Select a Doctor'
            )}
          </h2>
          {selectedDoctor ? (
            selectedDoctor.patients.length === 0 ? (
              <Card>
                <div className="p-8 text-center text-gray-500">
                  <svg className="w-16 h-16 mx-auto mb-4 text-gray-300" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M17 20h5v-2a3 3 0 00-5.356-1.857M17 20H7m10 0v-2c0-.656-.126-1.283-.356-1.857M7 20H2v-2a3 3 0 015.356-1.857M7 20v-2c0-.656.126-1.283.356-1.857m0 0a5.002 5.002 0 019.288 0M15 7a3 3 0 11-6 0 3 3 0 016 0zm6 3a2 2 0 11-4 0 2 2 0 014 0zM7 10a2 2 0 11-4 0 2 2 0 014 0z" />
                  </svg>
                  <p className="font-semibold text-gray-600">No patients yet</p>
                  <p className="text-sm text-gray-500 mt-1">This doctor hasn't been assigned any patients.</p>
                </div>
              </Card>
            ) : (
              selectedDoctor.patients.map((patient, index) => (
                <Card key={patient._id || patient.id || index} className="hover:shadow-lg transition-shadow">
                  <div className="p-6">
                    <div className="flex items-center gap-4">
                      <div className="w-10 h-10 bg-gradient-to-br from-green-500 to-emerald-500 rounded-full flex items-center justify-center">
                        <span className="text-white font-bold">
                          {patient.name?.charAt(0).toUpperCase() || '?'}
                        </span>
                      </div>
                      <div className="flex-1">
                        <h3 className="font-bold text-gray-900">{patient.name || 'Unknown Patient'}</h3>
                        {patient.email && <p className="text-sm text-gray-600">{patient.email}</p>}
                        {patient.contact && <p className="text-sm text-gray-600">{patient.contact}</p>}
                      </div>
                    </div>
                  </div>
                </Card>
              ))
            )
          ) : (
            <Card>
              <div className="p-12 text-center text-gray-500">
                <svg className="w-20 h-20 mx-auto mb-4 text-gray-300" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4.354a4 4 0 110 5.292M15 21H3v-1a6 6 0 0112 0v1zm0 0h6v-1a6 6 0 00-9-5.197M13 7a4 4 0 11-8 0 4 4 0 018 0z" />
                </svg>
                <p className="text-lg font-semibold text-gray-600 mb-2">No doctor selected</p>
                <p className="text-sm text-gray-500">
                  ðŸ‘ˆ Click on a doctor from the left to view their patients
                </p>
              </div>
            </Card>
          )}
        </div>
      </div>

      {/* Create Doctor Modal */}
      {showCreateModal && (
        <div className="fixed inset-0 bg-black bg-opacity-50 z-50 flex items-center justify-center p-4">
          <Card className="w-full max-w-md">
            <div className="p-6">
              <div className="flex items-center justify-between mb-6">
                <h2 className="text-2xl font-bold text-gray-900">Create New Doctor</h2>
                <button
                  onClick={() => setShowCreateModal(false)}
                  className="text-gray-400 hover:text-gray-600 transition-colors"
                >
                  <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                  </svg>
                </button>
              </div>

              <form onSubmit={handleCreateDoctor} className="space-y-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">Name</label>
                  <Input
                    type="text"
                    value={createForm.name}
                    onChange={(e) => setCreateForm({ ...createForm, name: e.target.value })}
                    placeholder="Doctor's full name"
                    required
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">Email</label>
                  <Input
                    type="email"
                    value={createForm.email}
                    onChange={(e) => setCreateForm({ ...createForm, email: e.target.value })}
                    placeholder="doctor@example.com"
                    required
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">Password</label>
                  <Input
                    type="password"
                    value={createForm.password}
                    onChange={(e) => setCreateForm({ ...createForm, password: e.target.value })}
                    placeholder="Secure password"
                    required
                  />
                </div>

                <div className="flex gap-3 mt-6">
                  <Button
                    type="button"
                    onClick={() => setShowCreateModal(false)}
                    className="flex-1 bg-gray-200 hover:bg-gray-300 text-gray-800"
                  >
                    Cancel
                  </Button>
                  <Button
                    type="submit"
                    className="flex-1 bg-gradient-to-r from-blue-600 to-indigo-600 hover:from-blue-700 hover:to-indigo-700 text-white"
                  >
                    Create Doctor
                  </Button>
                </div>
              </form>
            </div>
          </Card>
        </div>
      )}
    </div>
  );
}
